"use client";

import { useState, useEffect, type FC } from 'react';
import Link from 'next/link';
import { supabase } from '../../lib/supabase';
import Layout from '../../components/Layout';
import { Trophy, TrendingUp, Shield, ExternalLink, Github } from 'lucide-react';
import { Agent } from '../../types';

const LeaderboardPage: FC = () => {
    const [agents, setAgents] = useState<Agent[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const fetchLeaderboard = async () => {
            try {
                const { data, error } = await supabase
                    .from('agents')
                    .select('*, creator:profiles(*)')
                    .order('upvotes_count', { ascending: false })
                    .limit(50); // Top 50

                if (error) throw error;
                if (data) setAgents(data);
            } catch (error) {
                console.error('Error fetching leaderboard:', error);
            } finally {
                setIsLoading(false);
            }
        };

        fetchLeaderboard();
    }, []);

    return (
        <Layout>
            <div className="max-w-5xl mx-auto px-4 py-12 animate-in fade-in slide-in-from-bottom-4 duration-500">
                <div className="text-center mb-16">
                    <div className="inline-flex items-center gap-2 bg-black/5 px-4 py-1.5 rounded-full mb-6">
                        <Trophy className="w-4 h-4 text-yellow-600" />
                        <span className="text-sm font-medium text-gray-600 uppercase tracking-wider">Hall of Fame</span>
                    </div>
                    <h1 className="text-5xl md:text-6xl font-black tracking-tight mb-6">
                        Top Rated Agents
                    </h1>
                    <p className="text-xl text-gray-500 max-w-2xl mx-auto">
                        Discover the most popular and effective AI agents as voted by the community.
                    </p>
                </div>

                {isLoading ? (
                    <div className="space-y-4">
                        {[...Array(5)].map((_, i) => (
                            <div key={i} className="h-20 bg-gray-100 rounded-xl animate-pulse" />
                        ))}
                    </div>
                ) : (
                    <div className="bg-white border border-gray-200 rounded-2xl overflow-hidden shadow-sm">
                        <div className="overflow-x-auto">
                            <table className="w-full text-left">
                                <thead>
                                    <tr className="bg-gray-50 border-b border-gray-100">
                                        <th className="px-6 py-4 text-xs font-bold text-gray-500 uppercase tracking-wider w-16 text-center">Rank</th>
                                        <th className="px-6 py-4 text-xs font-bold text-gray-500 uppercase tracking-wider">Agent</th>
                                        <th className="px-6 py-4 text-xs font-bold text-gray-500 uppercase tracking-wider">Framework</th>
                                        <th className="px-6 py-4 text-xs font-bold text-gray-500 uppercase tracking-wider text-right">Upvotes</th>
                                        <th className="px-6 py-4 text-xs font-bold text-gray-500 uppercase tracking-wider text-right">Actions</th>
                                    </tr>
                                </thead>
                                <tbody className="divide-y divide-gray-100">
                                    {agents.map((agent, index) => (
                                        <tr key={agent.id} className="hover:bg-gray-50/50 transition-colors group">
                                            <td className="px-6 py-4 text-center">
                                                <span className={`
                          inline-flex items-center justify-center w-8 h-8 rounded-full font-bold text-sm
                          ${index === 0 ? 'bg-yellow-100 text-yellow-700' : ''}
                          ${index === 1 ? 'bg-gray-100 text-gray-700' : ''}
                          ${index === 2 ? 'bg-orange-100 text-orange-800' : ''}
                          ${index > 2 ? 'text-gray-500' : ''}
                        `}>
                                                    {index + 1}
                                                </span>
                                            </td>
                                            <td className="px-6 py-4">
                                                <Link href={`/agent/${agent.slug}`} className="flex items-center gap-4 group-hover:opacity-80 transition-opacity">
                                                    {/* We could use an agent avatar here if available, or a generic icon */}
                                                    <div className="w-10 h-10 bg-black text-white rounded-lg flex items-center justify-center shrink-0">
                                                        <span className="font-bold">{agent.name[0]}</span>
                                                    </div>
                                                    <div>
                                                        <div className="font-bold text-gray-900">{agent.name}</div>
                                                        <div className="text-sm text-gray-500 line-clamp-1">{agent.tagline}</div>
                                                    </div>
                                                </Link>
                                            </td>
                                            <td className="px-6 py-4">
                                                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800 border border-gray-200">
                                                    {agent.framework}
                                                </span>
                                            </td>
                                            <td className="px-6 py-4 text-right">
                                                <div className="inline-flex items-center gap-1 font-bold text-gray-900">
                                                    <TrendingUp className="w-4 h-4 text-green-500" />
                                                    {agent.upvotes_count}
                                                </div>
                                            </td>
                                            <td className="px-6 py-4 text-right">
                                                <Link
                                                    href={`/agent/${agent.slug}`}
                                                    className="inline-flex items-center justify-center px-4 py-2 border border-gray-200 shadow-sm text-sm font-medium rounded-lg text-gray-700 bg-white hover:bg-gray-50 hover:text-black hover:border-black transition-all"
                                                >
                                                    View
                                                </Link>
                                            </td>
                                        </tr>
                                    ))}

                                    {agents.length === 0 && (
                                        <tr>
                                            <td colSpan={5} className="px-6 py-12 text-center text-gray-500">
                                                No agents found.
                                            </td>
                                        </tr>
                                    )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )}
            </div>
        </Layout>
    );
};

export default LeaderboardPage;
